.header{
    display: flex;
    /* background-color: cadetblue; */
    justify-content: space-between;
    flex: 1;
    padding: 12px;
    position: relative;
    align-items: center;
}

.menu{
    /* background-color: blanchedalmond; */
    display: flex;
   flex: 1;
   justify-content: flex-end;
}

.menu-icon{
    font-size: 23px;
}

.mobile-menu{
    display: none;
}

.web-menu{
    display: flex;
}

.logo{
    display: flex;
    font-size: 36px;
    color: #3B79F9;
    font-weight: 800;
}

@media(max-width: 720px){
    .mobile-menu{
        display: flex;
    }
    .web-menu{
        display: none;
    }
    .logo{
        font-size: 24px;
    }
}

